.pagination button {
  width: 40px;
  height: 40px;
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 800;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  background-color: #ffbf09;
  margin: 5px 5px;
  }
  
  

  .pagination .active {
    font-weight: bold;
    background-color: #00c2ff;
    color: white;
  }

  .pagination{
    display: flex;
    padding-left: var(--Left-Padding);
    flex-direction: row;
    align-items: center;
    justify-content: center;
    user-select: none;
    flex-wrap: wrap;
  }

  .pagination_text{
    color: #fff;
    list-style: none;
    line-height: 45px;
    text-align: center;
    font-size: 18px;
    font-weight: 800;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }